import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"

import { CardDeck, Card } from "react-bootstrap" 
import cs101 from '../images/cs101.png';


const IndexPage = () => (
  <Layout>
    <SEO title="Courses" />
    <div className="container courses-container">
	    <h1>Courses</h1>
	    <br />

	    <CardDeck>

	      <Card>
		    <img src={cs101} />
		    <Card.Body>
		      <Card.Title>CS101</Card.Title>
		      <Card.Text>
		        <Link className="nav-link fab-nav-link" to="./cs101/">Intro to Computer Science | Spring 2020</Link>
		      </Card.Text>
		    </Card.Body>
		  </Card>

		  <Card>
		    <Image /> 
		    <Card.Body>
		      <Card.Title>ML101</Card.Title>
		      <Card.Text>
		        Machine Learning | Fall 2019
		      </Card.Text>
		    </Card.Body>
		  </Card>
		

		</CardDeck>
	</div>
  </Layout>
)

export default IndexPage

